@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

a, button:hover {
  cursor: pointer;
}

button::after, button::before:hover {
  cursor: pointer;
}

ul {
  list-style: none;
}

body {
  font-family: 'Comfortaa';
  background: #040D1F;
}

::after, ::before {
  font-family: 'fontawesome';
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.d-flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}

.aline-center {
  align-items: center;
}

.bdr-7 {
  border-radius: 7px;
}

.icon-film::before {
  content: '\f008';
  color: #ffffff;
  font-weight: 300;
}

.icon-play::before {
  content: '\f04b';
  color: #ffffff;
  font-weight: 900;
  position: absolute;
}

.icon-play::after {
  content: '\10f04b';
  color: #ffffff;
  font-weight: 900;
  opacity: 0.4;
}

.icon-burger::before {
  content: '\f0c9';
}

.btn {
  font-family: "Roboto";
  font-weight: 500;
  font-size: 14px;
  line-height: 12px;
  padding: 17px 48px;
  background: #F18128;
  border-radius: 4px;
  color: #000000;
  margin-right: 30px;
  display: inline-block;
}

.btn:hover {
  background: #040D1F;
  color: #ffffff;
}

.title {
  font-size: 36px;
  font-weight: 400;
  margin-bottom: 48px;
  margin-top: 60px;
  display: flex;
}

.title__link {
  color: #F18128;
  text-transform: capitalize;
}

.title__link::after {
  content: '';
  height: 2px;
  background: #F18128;
  display: block;
  margin-top: 2px;
}

.ads {
  background: linear-gradient(90.09deg, #3800AF 0%, #BF7204 97.52%, #C27500 100%, #C27500 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  color: #ffffff;
}

.ads__icon {
  line-height: 40px;
  font-size: 55px;
  margin-right: 24px;
}

.ads__info {
  font-size: 16px;
  max-width: 166px;
}

.header {
  background: rgba(1, 3, 5, 0.8);
  backdrop-filter: blur(15px);
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.nav a {
  color: #ffffff;
}

.nav__burger {
  background: none;
  outline: none;
  border: none;
  color: #ffffff;
  font-size: 35px;
  line-height: 40px;
  padding: 30px 10px;
  visibility: hidden;
}

@media (max-width: 768px) {
  .nav__burger {
    visibility: visible;
  }
}

.logo {
  font-weight: 700;
  font-size: 18px;
}

@media (max-width: 576px) {
  .logo {
    font-size: 15px;
  }
}

.logo img {
  margin-right: 5px;
}

@media (max-width: 576px) {
  .logo img {
    height: 32px;
  }
}

.menu {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .menu {
    position: absolute;
    right: 5px;
    top: 100px;
    background: rgba(1, 3, 5, 0.8);
    flex-direction: column;
    min-width: 200px;
    border-radius: 0 0 5px 5px;
    transform: scaleY(0);
    transition: 0.5s;
    transform-origin: 50% 0 0;
    opacity: 0;
  }
  .menu.active {
    opacity: 1;
    transform: scaleY(1);
  }
}

.menu__link {
  font-family: "Montserrat";
  font-size: 14px;
  padding: 43px 6px 40px 6px;
  display: block;
  margin-left: 18px;
  transition: 0.5s;
  position: relative;
}

@media (max-width: 768px) {
  .menu__link {
    padding: 20px;
    margin-left: 0;
  }
}

.menu__link::after {
  content: '';
  background: #ED8E00;
  height: 2px;
  width: 0;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transition: 0.5s;
  border-radius: 2px 2px 0px 0px;
}

@media (max-width: 768px) {
  .menu__link::after {
    width: 2px;
    height: 0;
    left: 0;
    bottom: 50%;
  }
}

.menu__link:hover {
  color: #ED8E00;
}

@media (max-width: 768px) {
  .menu__link:hover {
    background: rgba(237, 142, 0, 0.1);
  }
}

.menu__link:hover::after {
  width: 100%;
  left: 0;
}

@media (max-width: 768px) {
  .menu__link:hover::after {
    bottom: 0;
    width: 2px;
    height: 100%;
  }
}

.banner {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

.banner__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner__content {
  position: relative;
  background: rgba(7, 15, 25, 0.75);
  flex-grow: 1;
  padding: 15px 0;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  color: #ffffff;
}

.banner__title {
  font-size: 64px;
  font-weight: 700;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 768px) {
  .banner__title {
    font-size: 50px;
  }
}

@media (max-width: 576px) {
  .banner__title {
    font-size: 30px;
    text-align: center;
  }
}

.banner__desc {
  font-size: 16px;
  line-height: 24px;
  max-width: 570px;
  width: 100%;
  text-align: justify;
  color: #C8C8C8;
  font-weight: 400;
  margin-top: 22px;
  margin-bottom: 48px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 576px) {
  .banner__desc {
    text-align: center;
  }
}

.banner__links {
  display: flex;
  text-align: center;
}

@media (max-width: 576px) {
  .banner__links {
    flex-direction: column;
    align-items: center;
  }
  .banner__links .btn {
    margin-bottom: 20px;
    margin-right: 0;
  }
}

.films {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

@media (max-width: 992px) {
  .films {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .films {
    grid-template-columns: repeat(1, 1fr);
  }
}

.film {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.film__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.film__content {
  position: relative;
  background: linear-gradient(180deg, rgba(241, 129, 40, 0.35) 0%, rgba(0, 0, 0, 0.71) 51.04%, rgba(7, 15, 25, 0.8) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  padding: 56px 15px 20px 15px;
  font-family: "Montserrat";
}

.film__link {
  line-height: 48px;
  font-size: 50px;
}

.film__time {
  border: 1px solid #FFFFFF;
  border-radius: 9px;
  background: rgba(7, 15, 25, 0.2);
  padding: 7px 28px;
  font-size: 14px;
  margin-top: 13px;
  margin-bottom: 32px;
}

.film__title {
  font-size: 22px;
  font-weight: 400;
  margin-bottom: 13px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.film__desc {
  font-family: "Roboto";
  font-size: 14px;
  line-height: 21px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 6;
  overflow: hidden;
  text-overflow: ellipsis;
}

.film-banner {
  margin-top: 70px;
  position: relative;
  display: flex;
}

.film-banner__content {
  position: relative;
  background: linear-gradient(90deg, #DEF7FF 0%, #F4FCFF 45.64%, rgba(255, 255, 255, 0) 100.72%);
  padding-bottom: 68px;
  flex-grow: 1;
  display: flex;
  align-items: center;
  font-family: "Roboto";
  color: #000000;
}

@media (max-width: 992px) {
  .film-banner__content {
    margin: auto;
  }
}

@media (max-width: 445px) {
  .film-banner__content {
    padding-bottom: 30px;
    background: linear-gradient(90deg, #DEF7FF 0%, #F4FCFF 45.64%, #DEF7FF 100.72%);
  }
}

.film-banner__title {
  margin-top: 43px;
}

@media (max-width: 576px) {
  .film-banner__title {
    height: 70px;
  }
}

@media (max-width: 376px) {
  .film-banner__title {
    height: 55px;
  }
}

@media (max-width: 315px) {
  .film-banner__title {
    height: 48px;
  }
}

.film-banner__desc {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  max-width: 558px;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 55px;
}

@media (max-width: 576px) {
  .film-banner__desc {
    margin-top: 30px;
    margin-bottom: 35px;
    font-size: 17px;
    line-height: auto;
    height: 160px;
  }
}

@media (max-width: 445px) {
  .film-banner__desc {
    margin-bottom: 25px;
    height: 100%;
  }
}

@media (max-width: 376px) {
  .film-banner__desc {
    font-size: 15px;
  }
}

.film-banner__img {
  position: absolute;
  top: 0;
  right: 0;
  object-fit: cover;
}

@media (max-width: 576px) {
  .film-banner__img {
    height: 456px;
  }
}

@media (max-width: 445px) {
  .film-banner__img {
    display: none;
  }
}

.collections {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 48px;
}

@media (max-width: 768px) {
  .collections {
    grid-template-columns: repeat(1, 1fr);
  }
}

.collections__title {
  font-size: 22px;
  font-weight: 400;
  margin-bottom: 13px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.collection {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border-radius: 6px;
}

.collection__icon {
  line-height: 48px;
  font-size: 50px;
  margin-bottom: 16px;
}

.collection__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.collection__desc {
  font-family: "Comfortaa";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
  color: #ffffff;
}

.collection__content {
  position: relative;
  background: linear-gradient(270deg, rgba(7, 15, 25, 0.56) 0%, rgba(49, 114, 9, 0.56) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 59px;
  padding-bottom: 60px;
}

.series {
  width: 100%;
  color: #ffffff;
  margin-bottom: 60px;
}

.series__throneGame {
  position: relative;
  height: 450px;
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .series__throneGame {
    margin-bottom: 60px;
  }
}

.series__films {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 400px;
  gap: 30px;
}

@media (max-width: 768px) {
  .series__films {
    grid-template-columns: repeat(1, 1fr);
  }
}

.series__films-second {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

@media (max-width: 768px) {
  .series__films-second {
    height: 400px;
    gap: 15px;
  }
}

.series__film {
  position: relative;
}

.series__film-content {
  position: relative;
  background: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);
  height: 100%;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.series__film > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 7px;
  object-fit: cover;
}

.gameThrone__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 7px;
  object-fit: cover;
}

.gameThrone__content {
  position: relative;
  flex-grow: 1;
  height: 100%;
  background: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 7px;
}

.gameThrone__title {
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .gameThrone__title {
    font-size: 28px;
  }
}

.gameThrone__desc {
  font-size: 16px;
  padding-right: 10px;
  padding-left: 10px;
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .gameThrone__desc {
    font-size: 15px;
  }
}

.footer {
  background: linear-gradient(90deg, #FF8C30 0%, #9F4700 100%);
  display: flex;
}

.footer__info {
  display: flex;
  align-items: center;
}

@media (max-width: 576px) {
  .footer__info {
    margin: auto;
    margin-bottom: 30px;
  }
}

.footer__images {
  display: flex;
}

.footer__images > a > img {
  margin-right: 30px;
}

@media (max-width: 576px) {
  .footer__images > a > img {
    margin-right: 0;
  }
}

@media (max-width: 576px) {
  .footer__images {
    justify-content: space-evenly;
  }
}

.footer__content {
  color: #ffffff;
  font-size: 18px;
  padding: 30px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 576px) {
  .footer__content {
    flex-direction: column-reverse;
    padding: 15px;
  }
}
