.series {
    width: 100%;
    color: $white;
    margin-bottom: 60px;
    
    &__throneGame { 
        position: relative;
        height: 450px;
        margin-bottom: 30px; 

        @include media($sm) {
            margin-bottom: 60px;
        }
    }

    &__films {       
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 400px;
        gap: 30px;    

        @include media($sm) {
            grid-template-columns: repeat(1, 1fr);
        }

        &-second {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 30px; 

            @include media($sm) {
                height: 400px;
                gap: 15px;
            }
        }
    }

    &__film {
        position: relative;

        &-content {
            position: relative;
            background: $series;
            height: 100%; 
            border-radius: 7px;  
            display: flex;   
            flex-direction: column;
            justify-content: center;
            align-items: center; 
        }

        & > img {
            position: absolute; 
            top: 0;
            left: 0;
            width: 100%;
            height: 100%; 
            border-radius: 7px; 
            object-fit: cover;
        }
    }
}

.gameThrone {
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;  
        border-radius: 7px; 
        object-fit: cover;
    }

    &__content {
        position: relative;
        flex-grow: 1;
        height: 100%;
        background: $series;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        border-radius: 7px; 
    }

    &__title {
        font-weight: 700;
        font-size: 40px;
        margin-bottom: 30px;

        @include media($sm) {
            font-size: 28px;
        }
    }

    &__desc {
        font-size: 16px;            
        padding-right: 10px;
        padding-left: 10px;
        margin-bottom: 30px;

        @include media($sm) {
            font-size: 15px;
        }
    }
}