* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

a, button:hover {
    cursor: pointer;
}

button::after, button::before:hover {
    cursor: pointer;
}

ul {
    list-style: none;
}

body {
    font-family: 'Comfortaa';
    background: $main;
}

::after, ::before {
    font-family: 'fontawesome';
}

.container {
    @include container();
}

.row {
    @include row();
}

.d-flex {
    display: flex;
}

.flex-column {
    flex-direction: column;   
}

.justify-center {
    justify-content: center;
}

.aline-center {
    align-items: center;
}

.bdr-7 {
    border-radius: 7px; 
}

.icon {
    &-film::before {
        content: '\f008';
        color: $white;
        font-weight: 300;
    }

    &-play::before {
        content: '\f04b';
        color: $white;
        font-weight: 900;
        position: absolute;
    }
    
    &-play::after {
        content: '\10f04b';
        color: $white;
        font-weight: 900;
        opacity: 0.4;
    }

    &-burger::before {
        content: '\f0c9';
    }
}

.btn {
    font-family: "Roboto";
    font-weight: 500;
    font-size: 14px;
    line-height: 12px;
    padding: 17px 48px;
    background: $orange;
    border-radius: 4px;
    color: $black;
    margin-right: 30px;
    display: inline-block;
    
    &:hover {
        background: $main;
        color: $white;
    }
}

.title {
    font-size: 36px;
    font-weight: 400;
    margin-bottom: 48px;
    margin-top: 60px;
    display: flex;
    &__link {
        color: $orange;
        text-transform: capitalize;

        &::after {
            content: '';
            height: 2px;
            background: $orange;
            display: block;
            margin-top: 2px;
        }
    }
}