.footer {
    background: $footer; 
    display: flex;

    &__info {
        display: flex;
        align-items: center; 
        @include media($xs) {
            margin: auto;
            margin-bottom: 30px; 
        }
    }

    &__images {
        display: flex;

        & > a > img {
            margin-right: 30px; 

            @include media($xs) {
                margin-right: 0; 
            }
        }
        
        @include media($xs) {
            justify-content: space-evenly;
        }
    }

    &__content {
        color: $white;
        font-size: 18px;
        padding: 30px;
        display: flex;
        justify-content: space-between;

        @include media($xs) { 
            flex-direction: column-reverse;
            padding: 15px;
        }
    }
}