.films {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;

    @include media($md) {
        grid-template-columns: repeat(2, 1fr);

    }

    @include media($xs) {
        grid-template-columns: repeat(1, 1fr);
        
    }
}

.film {
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        position: relative;
        background: $film;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $white;
        padding: 56px 15px 20px 15px;
        font-family: "Montserrat";
    }

    &__link {
        line-height: 48px;
        font-size: 50px;
    }

    &__time {
        border: 1px solid #FFFFFF;
        border-radius: 9px;
        background: $time;
        padding: 7px 28px;
        font-size: 14px;
        margin-top: 13px;    
        margin-bottom: 32px;
    }

    &__title {
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 13px;
        @include limit(1);
    }
    
    &__desc {
        font-family: "Roboto";
        font-size: 14px;
        line-height: 21px;
        @include limit(6);
    }
}


.film-banner {
    margin-top: 70px;
    position: relative; 
    display: flex;
    
    &__content {
        position: relative;
        background: $pr-film;
        padding-bottom: 68px; 
        flex-grow: 1;
        display: flex;
        align-items: center;
        font-family: "Roboto"; 
        color: $black;

        @include media($md) {
            margin: auto; 
        }

        
        @include media(445px) {
            padding-bottom: 30px; 
            background: linear-gradient(90deg, #DEF7FF 0%, #F4FCFF 45.64%, #DEF7FF 100.72%);
        }
    }

    &__title {
        margin-top: 43px; 
        @include media($xs) { 
            height: 70px;   
        }

        @include media(376px) {
            height: 55px;
        } 

        @include media(315px) {
            height: 48px; 
        } 
    }

    &__desc {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 27px;
        max-width: 558px;
        width: 100%;
        margin-top: 50px;  
        margin-bottom: 55px;   

        @include media($xs) { 
            margin-top: 30px; 
            margin-bottom: 35px;
            font-size: 17px;
            line-height: auto;
            height: 160px;
        }

        @include media(445px) {
            margin-bottom: 25px; 
            height: 100%; 
        }

        @include media(376px) {
            font-size: 15px;
        }
    }

    &__img { 
        position: absolute;
        top: 0;
        right: 0; 
        object-fit: cover; 

        @include media($xs) { 
            height: 456px; 
        }

        @include media(445px) {
            display: none; 
        }
    }
}