.collections {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-top: 48px;  

    @include media($sm) {
        grid-template-columns: repeat(1, 1fr);
    }

    &__title {
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 13px;
        @include limit(1);
    }
}

.collection {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    border-radius: 6px;

    &__icon {
        line-height: 48px;
        font-size: 50px;
        margin-bottom: 16px;
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    } 

    &__desc {
        font-family: "Comfortaa";
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 22px;
        color: $white;
    }

    &__content {
        position: relative;
        background: $coll-film;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 59px;
        padding-bottom: 60px;
    }

    
}